.timeline {
  position: relative;
  width: 100%;
  padding: 30px 0;
}
@media (max-width: 767.98px) {
  .timeline {
    padding: 0;
  }
}
.timeline .timeline-end,
.timeline .timeline-start,
.timeline .timeline-year {
  position: relative;
  width: 100%;
  text-align: center;
  z-index: 1;
}
.timeline .timeline-end p,
.timeline .timeline-start p,
.timeline .timeline-year p {
  display: inline-block;
  width: 80px;
  height: 80px;
  margin: 0;
  padding: 30px 0;
  text-align: center;
  background-color: #1c84ee;
  border-radius: 100px;
  color: #fff;
  text-transform: uppercase;
}
.timeline .timeline-year {
  margin: 30px 0;
}
.timeline .timeline-continue {
  position: relative;
  width: 100%;
  padding: 60px 0;
}
.timeline .timeline-continue:after {
  position: absolute;
  content: "";
  width: 1px;
  height: 100%;
  top: 0;
  left: 50%;
  margin-left: -1px;
  background: #1c84ee;
}
.timeline .timeline-icon {
  margin: 42px 10px 0 10px;
}
.timeline .timeline-left {
  text-align: right;
}
.timeline .timeline-left .timeline-icon {
  text-align: left;
}
.timeline .timeline-right {
  text-align: left;
}
.timeline .timeline-right .timeline-icon {
  text-align: right;
}
.timeline .timeline-icon::after {
  content: "";
  display: block;
  position: absolute;
  width: 14px;
  height: 14px;
  top: 45px;
  background: #1c84ee;
  border-radius: 15px;
  z-index: 1;
}
@media (min-width: 768px) {
  .timeline .event-content {
    padding-right: 24px;
  }
  .timeline .timeline-text {
    margin-right: 40px;
  }
}
.timeline .timeline-left .timeline-icon::after {
  left: -7px;
}
@media (min-width: 768px) {
  .timeline .timeline-left .event-content {
    padding-right: 0;
    padding-left: 24px;
  }
  .timeline .timeline-left .timeline-text {
    margin-right: 0px;
    margin-left: 40px;
  }
  .timeline .timeline-left .event-img {
    justify-content: flex-end;
  }
}
.timeline .timeline-right .timeline-icon::after {
  right: -7px;
}
.timeline .timeline-box {
  position: relative;
  display: inline-block;
  margin: 15px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0.25rem 0.75rem rgba(18, 38, 63, 0.08);
  border-radius: 6px;
}
.timeline .timeline-box::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: 26px;
  background-color: #fff;
  border: 1px solid #f6f6f6;
  transform: rotate(45deg);
  margin: 0px auto;
}
.timeline .timeline-launch {
  position: relative;
  display: inline-block;
  margin: 15px;
  padding: 20px;
  border: 1px solid #e9e9ef;
  border-radius: 6px;
  width: 100%;
  margin: 15px 0;
  padding: 0;
  border: none;
  text-align: center;
  background: transparent;
}

.timeline-date {
  right: 20px;
}
@media (max-width: 767.98px) {
  .timeline-date {
    left: -70px;
    right: auto;
  }
}

.timeline-left .timeline-date {
  left: 20px;
}
@media (max-width: 767.98px) {
  .timeline-left .timeline-date {
    left: -70px;
    right: auto;
  }
}

.timeline .timeline-date {
  width: 54px;
  height: 80px;
  display: inline-block;
  padding: 8px;
  clip-path: polygon(0 0, 100% 0, 100% 80%, 50% 100%, 0 80%);
  top: -10px;
  position: absolute;
  z-index: 1;
}
@media (max-width: 767.98px) {
  .timeline .timeline-date {
    top: 0px;
  }
}
.timeline .timeline-right .timeline-box::after {
  border-color: transparent transparent #f6f6f6 #f6f6f6;
  left: -8px;
  right: 100%;
}
.timeline .timeline-left .timeline-box::after {
  border-color: #f6f6f6 #f6f6f6 transparent transparent;
  right: -8px;
}
.timeline .timeline-launch .timeline-box::after {
  top: -8px;
  left: 32px;
  border-color: #f6f6f6 transparent transparent #f6f6f6;
}
@media (min-width: 768px) {
