  }
  .timeline .row.timeline-right .timeline-icon {
    text-align: left;
  }
  .timeline .timeline-launch {
    text-align: left;
    margin-bottom: 0;
  }
  .timeline .row.timeline-left .timeline-icon::after {
    left: 43px;
  }
  .timeline .row.timeline-right .timeline-icon::after {
    left: 43px;
  }
  .timeline .timeline-box {
    margin-left: 82px;
