  .timeline .row.timeline-right .timeline-icon {
    margin-left: 55px;
  }
  .timeline .row.timeline-left .timeline-icon {
    margin-left: 55px;
  }
  .timeline .timeline-launch .timeline-box {
    margin-left: 0;
    margin-bottom: 0;
  }
  .timeline .row.timeline-left .timeline-box::after {
